.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding: 20px 50px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 100;

  @media (max-width: 768px) {
    padding: 20px 30px;
  }
  @media (max-width: 480px) {
    padding: 10px 20px;
    background-color: rgba(255,255,255,0.9);
  }
}

.name {
  font-size: 1.5rem;
}

.email {
  text-decoration: none;
  color: #333;
  transition: all 0.2s ease;
  font-size: 0.875rem;
  &_wrap {
    overflow: hidden;
    position: relative;
  }
  &_title {
    transition: transform 0.4s cubic-bezier(0.6, 0.25, 0, 1);
    display: block;
  }
  &_title:after {
    content: attr(data-attr);
    position: absolute;
    bottom: 0;
    left: 0;
    transition: 0.5S;
    transform-origin: top;
    transform: translateY(104%);
  }
  &:hover {
    .email_title {
      transform: translateY(-104%);
    }
  }
}
