.footer {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  padding: 20px 50px;
  @media (max-width: 768px) {
    padding: 20px 30px;
  }
  @media (max-width: 480px) {
    padding: 12px 20px 8px 20px;
    background-color: rgba(255,255,255,0.9);
  }
}


.social-links {
  &_item {
    text-decoration: none;
    margin-right: 20px;
    @media (max-width: 480px) {
      margin-right: 14px;
    }
    &:last-child {
      margin-right: 0;
    }
    &:hover {
      svg path {
        fill: $accent;
      }
    }
    svg {
      @media (max-width: 480px) {
        width: 20px;
        height: 20px;
      }
      path {
        transition: all 0.2s ease;
      }
    }
  }
}
