.main {
  padding: 100px 50px;
  @media (max-width: 768px) {
    padding: 100px 30px;
  }
  @media (max-width: 480px) {
    padding: 80px 20px;
  }
}

.section-works {
  max-width: 834px;
  margin: 0 auto;
}

.title {
  font-size: 3rem;
  text-transform: uppercase;
}

.works {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.work {
  text-decoration: none;
  color: #333;
  width: 48%;
  display: block;
  margin-bottom: 100px;
  @media (max-width: 480px) {
    margin-bottom: 50px;
  }
  &:hover {
    color: $accent;
    img {
      filter: blur(2px);
    }
  }
  &_img {
    height: 300px;
    width: 100%;
    background-color: #cfcfcf;
    margin-bottom: 25px;
    overflow: hidden;
    box-shadow: 0 0 6px rgba(0,0,0,0.1);
    @media (max-width: 768px) {
      height: 220px;
    }
    @media (max-width: 480px) {
      height: 120px;
      margin-bottom: 15px;
    }
    img {
      transition: all 0.2s ease;
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
    }
  }
  &_title {
    margin-bottom: 10px;
    font-size: 1.125rem;
    @media (max-width: 480px) {
      margin-bottom: 5px;
    }
  }
  &_subtitle {
    font-size: 1rem;
  }
}
